










import * as echarts from '../../common/echarts/echarts.simple.min.js';
import mpvueEcharts from '../../common/echarts/src/echarts.vue';
import pageTabber from '../common/tabber.vue';
function getPieOption() {
		return {
			animation: false,
			backgroundColor: '#F8F8F8',
			color: ['#37A2DA', '#32C5E9', '#67E0E3', '#91F2DE', '#FFDB5C', '#FF9F7F'],
			series: [{
				label: {
					normal: {
						fontSize: 14
					}
				},
				type: 'pie',
				center: ['50%', '50%'],
				radius: [0, '60%'],
				data: [{
					value: 55,
					name: '北京'
				}, {
					value: 20,
					name: '武汉'
				}, {
					value: 10,
					name: '杭州'
				}, {
					value: 20,
					name: '广州'
				}, {
					value: 38,
					name: '上海'
				}],
				itemStyle: {
					emphasis: {
						shadowBlur: 10,
						shadowOffsetX: 0,
						shadowColor: 'rgba(0, 2, 2, 0.3)'
					}
				}
			}]
		}
	}
export default {
	data() {
		return {
			echarts,
			pieInit: function (canvas, width, height) {
				let pieChart = echarts.init(canvas, null, {
					width: width,
					height: height
				})
				canvas.setChart(pieChart)

				pieChart.setOption(getPieOption())
				return pieChart
			},
		}
	},
	components: {
		mpvueEcharts,
		pageTabber
	},
	onLoad: function () {
		// this.newsitems = this.randomfn()
	},
	onUnload:function(){
		this.scrollLeft = 0,
		this.isClickChange = false,
		this.currentTab = 0;
	},
	methods: {
	},
	mounted(){
		uni.getSystemInfo({
			success:res=>{
				this.height = res.screenHeight;
			}
		})
	}
}





